<div class="select-wrapper"><select v-model="dataCenterSelect"><option v-for="dataCenter in dataCenters" :value="dataCenter">{{dataCenter}}</option></select></div><div class="select-wrapper" v-if="dataCenterSelect === 'Elemental'"><select v-model="worldElemental"><option v-for="elemental in elementalDc" :value="elemental">{{elemental}}</option></select></div><div class="select-wrapper" v-if="dataCenterSelect === 'Gaia'"><select v-model="worldGaia"><option v-for="Gaia in gaiaDc" :value="Gaia">{{Gaia}}</option></select></div><div class="select-wrapper" v-if="dataCenterSelect === 'Mana'"><select v-model="worldMana"><option v-for="mana in manaDc" :value="mana">{{mana}}</option></select></div><div class="select-wrapper" v-if="dataCenterSelect === 'Meteor'"><select v-model="worldMeteor"><option v-for="meteor in meteorDc" :value="meteor">{{meteor}}</option></select></div>