<div class="job-chart-column" v-if="Columns &gt; 0"><span class="title">ジョブ</span><div class="select-wrapper"><select v-model="selectJob1"><option v-for="job in jobs" :value="job.id">{{job.name}}</option></select></div><span class="title">練度</span><input type="number" id="jobChartPoint1" value="50" @change="jobChartPoint1" min="0" max="100" placeholder="1~100までを設定"></div><div class="job-chart-column" v-if="Columns &gt; 1"><span class="title">ジョブ</span><div class="select-wrapper"><select v-model="selectJob2"><option v-for="job in jobs" :value="job.id">{{job.name}}</option></select></div><span class="title">練度</span><input type="number" id="jobChartPoint2" value="50" @change="jobChartPoint2" min="0" max="100"></div><div class="job-chart-column" v-if="Columns &gt; 2"><span class="title">ジョブ</span><div class="select-wrapper"><select v-model="selectJob3"><option v-for="job in jobs" :value="job.id">{{job.name}}</option></select></div><span class="title">練度</span><input type="number" id="jobChartPoint3" value="50" @change="jobChartPoint3" min="0" max="100"></div><div class="job-chart-column" v-if="Columns &gt; 3"><span class="title">ジョブ</span><div class="select-wrapper"><select v-model="selectJob4"><option v-for="job in jobs" :value="job.id">{{job.name}}</option></select></div><span class="title">練度</span><input type="number" id="jobChartPoint4" value="50" @change="jobChartPoint4" min="0" max="100"></div><div class="job-chart-column" v-if="Columns &gt; 4"><span class="title">ジョブ</span><div class="select-wrapper"><select v-model="selectJob5"><option v-for="job in jobs" :value="job.id">{{job.name}}</option></select></div><span class="title">練度</span><input type="number" id="jobChartPoint5" value="50" @change="jobChartPoint5" min="0" max="100"></div><div class="button-list"><button class="add-button" type="button" @click="addJob" v-if="Columns &lt; 5">ジョブを増やす<span>＋</span></button><button class="delete-button" type="button" @click="deleteJob" v-if="Columns &gt; 1">ジョブを減らす<span>－</span></button></div>