<div class="input-field-wrap"><div class="input-field-inner"><dl class="input-column"><dt>カードタイプ</dt><dd><p class="notice">カードの背景、ロールアイコンが変わります</p><label v-for="cardType in cardTypeArr"><input type="radio" name="cardType" v-model="cardTypeSelect" :value="cardType.class"><span>{{ cardType.name }}</span></label></dd></dl><dl class="input-column"><dt>キャラクター画像</dt><dd><p class="notice">縦600px、縦300pxまたは2:1比率の縦長画像推奨。<br>画像を選択するとトリミング画面が表示されますので、お好みの部分でトリミングしてください。</p><div class="image-input"><input @change="setImage" type="file" name="image" accept="image/*"></div><ImageCropper></ImageCropper></dd></dl><dl class="input-column"><dt>キャラクター名</dt><dd><input class="full-width" type="url" v-model="changeName" id="charactorName" placeholder="キャラクター名" @keyup="keyUpHalfSizeRestriction"></dd></dl><dl class="input-column"><dt>DC/ワールド</dt><dd><ServerSelect></ServerSelect></dd></dl><dl class="input-column"><dt>募集コンテンツ名</dt><dd><p class="notice">募集するコンテンツを入力してください</p><input class="full-width" type="text" v-model="contentsName" id="contentsName" placeholder="例：万魔殿パンデモニウム：天獄編"></dd></dl><dl class="input-column"><dt>活動可能時間</dt><dd><div class="select-wrapper"><select v-model="startTimeSelect"><option v-for="startTime in startTimesArr" :value="startTime">{{ startTime }}</option></select></div><span>時～</span><div class="select-wrapper"><select v-model="endTimeSelect"><option v-for="endTime in endTimesArr" :value="endTime">{{ endTime }}</option></select></div><span>時</span></dd></dl><dl class="input-column"><dt>VCの可否</dt><dd><label v-for="vc in vcArr"><input type="radio" name="vc" :value="vc" v-model="vcSelect"><span>{{vc}}</span></label></dd></dl><dl class="input-column"><dt>DCトラベル対応</dt><dd><label v-for="dcTravel in dcTravelArr"><input type="radio" name="dcTravel" :value="dcTravel" v-model="dcTravelSelect"><span>{{dcTravel}}</span></label></dd></dl><dl class="input-column"><dt>クリア目標</dt><dd><p class="notice">希望するおおよその攻略期間を選択してください</p><div class="select-wrapper"><select v-model="clearPeriodSelect"><option v-for="clearPeriod in clearPeriodArr" :value="clearPeriod">{{ clearPeriod }}</option></select></div></dd></dl><dl class="input-column"><dt>活動頻度</dt><dd><p class="notice">週にどれほど活動したいかの目安を選んでください</p><div class="select-wrapper"><select v-model="frequencySelect"><option v-for="frequency in frequencyArr" :value="frequency">{{ frequency }}</option></select></div><div class="select-wrapper"><select v-model="standardSelect"><option v-for="standard in standardArr" :value="standard">{{ standard }}</option></select></div><div class="weekly"><p class="notice">特定の曜日に活動できない、または活動ができるという場合その曜日を表示することができます</p><div class="wrap"><label v-for="week in weekArr"><input type="checkbox" :value="week" name="week" v-model="weekUpdate"><span>{{week}}</span></label></div><div class="wrap"><label v-for="yesOrNo in yesOrNos"><input type="radio" name="yesOrNo" :value="yesOrNo" v-model="yesOrNoSelect"><span>{{yesOrNo}}</span></label></div></div></dd></dl><dl class="input-column"><dt>可能なジョブおよび練度</dt><dd><p class="notice">対応可能なジョブの選択と、そのジョブに対する練度を自己評価で0～100の間で入力してください（最大5つまで）<br><span>補足：今回一番出したいジョブを100とし、その他のジョブはそれに大して相対的にこれくらい、といったような具合で気楽に評価してください</span></p><JobChart></JobChart></dd></dl><dl class="input-column"><dt>コメント</dt><dd><p class="notice">自己紹介や意気込み、その他載せたい情報を記入してください<br>枠外にはみ出てしまう場合は省略されます</p><textarea v-model="comment" id="comment" placeholder="例：クリアに向けて頑張りますのでよろしくお願いします！"></textarea></dd></dl></div><div class="scroll-preview" @click="scrollPreview">カードのプレビューを見る</div></div><div class="crop-modal-wrapper" v-if="imgSrc !== ''"><div class="croppwer-container"><vue-cropper ref="cropper" :guides="true" :view-mode="1" :auto-crop-area="1" :min-container-width="600" :min-container-height="600" :background="false" :rotatable="false" :src="imgSrc" :img-style="{ 'width': '300px', 'height': '600px' }" :aspect-ratio="targetWidth / targetHeight" drag-mode="crop"></vue-cropper><p class="notice pc">マウスのスクロールで画像のズームを調整可能です</p><p class="notice sp">ピンチイン、ピンチアウトで画像のズームを調整可能です</p><button @click="cropImage" v-if="imgSrc !== ''">トリミングを反映する</button></div></div>