<div class="card-section" id="card"><div class="card-inner"><h2 class="title">CARD PREVIEW</h2><div id="card-preview"><div class="card-wrap" :class="[cardType.class ? cardType.class : 'type-tank']"><div class="frame"></div><div class="card-inner"><div class="card-column"><div class="chara-img"><img :src="[imageUrl ? imageUrl : require('@/assets/images/no-image.png')]"></div><div class="param"><div class="name-block"><div class="job-icon"><img :src="[cardType.class ? require(`@/assets/images/roles/${cardType.class}.png`) : require('@/assets/images/roles/type-tank.png')]"></div><div class="profile"><div class="name">{{charactorName}}</div><div class="world">{{dataCenter}} / {{world}}</div></div></div><div class="contents-block"><div class="block-inner"><div class="top-block"><dl class="type contents"><dt>募集コンテンツ</dt><dd>{{contentsName}}</dd></dl></div><div class="mid-block"><div class="flex-block"><dl class="type time"><dt>活動可能時間</dt><dd>{{startTimeSelect}}時～{{endTimeSelect}}時</dd></dl><dl class="type vc"><dt>VC</dt><dd>{{vcCheck}}</dd></dl><dl class="type travel"><dt>DCトラベル</dt><dd>{{dcTravelCheck}}</dd></dl></div></div><div class="bottom-block"><div class="flex-block"><dl class="type period"><dt>クリア目標</dt><dd>{{clearPeriod}}</dd></dl><dl class="type frequency"><dt>活動頻度</dt><dd><span>{{frequency}}{{standard}}</span><span class="week" v-if="weekUpdate">（{{weekUpdate}}</span><span v-if="weekUpdate">{{yesOrNo}}）</span></dd></dl></div></div></div></div><div class="bottom-prof-block"><div class="job-chart flex-block"><div class="title">PROFICIENCY LEVELS</div><div class="chart-block"><ul class="chart"><li v-if="jobCount &gt; 0"><div class="icon"><img :src="[selectJob1.id ? require(`@/assets/images/jobs/${selectJob1.id}.png`) : require('@/assets/images/jobs/Paladin.png')]"></div><div class="meter-wrap"><div class="meter" :class="`w${jobPoint1}`"></div></div></li><li v-if="jobCount &gt; 1"><div class="icon"><img :src="[selectJob2.id ? require(`@/assets/images/jobs/${selectJob2.id}.png`) : require('@/assets/images/jobs/Paladin.png')]"></div><div class="meter-wrap"><div class="meter" :class="`w${jobPoint2}`"></div></div></li><li v-if="jobCount &gt; 2"><div class="icon"><img :src="[selectJob3.id ? require(`@/assets/images/jobs/${selectJob3.id}.png`) : require('@/assets/images/jobs/Paladin.png')]"></div><div class="meter-wrap"><div class="meter" :class="`w${jobPoint3}`"></div></div></li><li v-if="jobCount &gt; 3"><div class="icon"><img :src="[selectJob4.id ? require(`@/assets/images/jobs/${selectJob4.id}.png`) : require('@/assets/images/jobs/Paladin.png')]"></div><div class="meter-wrap"><div class="meter" :class="`w${jobPoint4}`"></div></div></li><li v-if="jobCount &gt; 4"><div class="icon"><img :src="[selectJob5.id ? require(`@/assets/images/jobs/${selectJob5.id}.png`) : require('@/assets/images/jobs/Paladin.png')]"></div><div class="meter-wrap"><div class="meter" :class="`w${jobPoint5}`"></div></div></li></ul></div></div><div class="comment flex-block"><div class="title">COMMENT</div><div class="comment-block"><p>{{comment}}</p></div></div></div><div class="copyright">(C) SQUARE ENIX CO., LTD. All Rights Reserved.</div></div></div></div></div></div><div class="button-wrap"><button class="download" @click="caputureImage" v-if="userAgent !== 'SmartPhone'">カード画像をダウンロード</button><button class="download" @click="caputureImage" v-if="userAgent === 'SmartPhone'">カード画像を表示</button><p class="safari-notice" v-if="userAgent === 'Safari'">Safariのみ画像をダウンロードする際に一部画像が表示されない不具合があります。<br>ダウンロードし直すことで解消される場合がありますので、お手数ですが再ダウンロードをお願いします。</p></div></div></div><div class="card-modal" v-if="spCardCreate !== ''"><div class="modal-inner"><div id="card-image"></div><p>画像を長押しして端末に保存してください</p><button @click="cardModalClose">閉じる</button></div></div>